import React, { useRef, useEffect, useState } from "react";
import toast, { Toaster } from "react-hot-toast";
import * as faceapi from "face-api.js";
import "./FaceRecognition.css";

const TERMINAL_ID = "2";
const DIRECTION = "1";
const BASE_URL = "https://kitvision.pavlodar.dev:3003";

const FaceRecognition = () => {
    const videoRef = useRef(null);
    const fullScreenRef = useRef(null);
    const errorOverlayRef = useRef(null);

    const [loadingLibraries, setLoadingLibraries] = useState(true);
    const [cameras, setCameras] = useState([]);
    const [selectedCamera, setSelectedCamera] = useState(null);
    const [rotation, setRotation] = useState("0");
    const [showControls, setShowControls] = useState(false);
    const [lastUserName, setLastUserName] = useState("");
    const [photoUrl, setPhotoUrl] = useState("");
    const [error, setError] = useState(null);

    const [terminalId, setTerminalId] = useState(TERMINAL_ID);
    const [direction, setDirection] = useState(DIRECTION);

    // Храним ImageCapture, чтобы делать "сырые" снимки
    const [imageCapture, setImageCapture] = useState(null);

    /**
     * 1. Загружаем модели face-api
     */
    useEffect(() => {
        const loadModels = async () => {
            try {
                // Обратите внимание на путь ./models:
                // на продакшене убедитесь, что файлы в папке /models действительно доступны.
                await faceapi.nets.tinyFaceDetector.loadFromUri("./models");
                console.log("Модели распознавания лиц загружены");
            } catch (err) {
                console.error("Ошибка при загрузке моделей:", err);
                toast.error("Ошибка при загрузке моделей распознавания лиц.");
            }
        };

        loadModels();
    }, []);

    /**
     * 2. Восстанавливаем настройки (ID турникета, направление) из localStorage
     */
    useEffect(() => {
        if (localStorage.getItem("terminalId")) {
            setTerminalId(localStorage.getItem("terminalId"));
        }
        if (localStorage.getItem("direction")) {
            setDirection(localStorage.getItem("direction"));
        }
    }, []);

    /**
     * 3. Получаем список камер
     */
    useEffect(() => {
        const initializeDevices = async () => {
            try {
                if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
                    toast.error("Ваш браузер не поддерживает getUserMedia.");
                    return;
                }

                // Проверяем, что HTTPS
                if (window.location.protocol !== "https:" && window.location.hostname !== "localhost") {
                    toast.error("Для доступа к камере требуется HTTPS.");
                    return;
                }

                // Запрашиваем доступ к камере перед `enumerateDevices()`
                await navigator.mediaDevices.getUserMedia({ video: true });

                const devices = await navigator.mediaDevices.enumerateDevices();
                console.log("Devices:", devices);

                const videoDevices = devices.filter((d) => d.kind === "videoinput");
                console.log("Video devices:", videoDevices);

                if (videoDevices.length === 0) {
                    toast.error("Камера не найдена.");
                    return;
                }

                setCameras(videoDevices);
                setSelectedCamera(videoDevices[0].deviceId);
            } catch (err) {
                console.error("Ошибка при получении списка камер:", err);
                toast.error("Ошибка при доступе к камере.");
            }
        };

        initializeDevices();
    }, []);

    /**
     * 4. Показываем "Загрузка" пока камера не запущена
     */
    useEffect(() => {
        if (loadingLibraries) {
            toast.loading("Загрузка...");
        } else {
            toast.dismiss();
        }
    }, [loadingLibraries]);

    /**
     * 5. Запуск камеры при выборе устройства или смене поворота
     */
    useEffect(() => {
        if (selectedCamera) {
            startCamera(selectedCamera);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [selectedCamera, rotation]);

    const startCamera = async (deviceId) => {
        try {
            // Проверка протокола: браузеры требуют HTTPS (либо localhost)
            if (
                window.location.protocol !== "https:" &&
                window.location.hostname !== "localhost"
            ) {
                toast.error("Для доступа к камере требуется HTTPS.");
                console.error("Страница не по HTTPS, getUserMedia может быть заблокирован.");
                setLoadingLibraries(false);
                return;
            }

            // Настраиваем запрос видео
            const constraints = {
                video: {
                    deviceId: deviceId ? { exact: deviceId } : undefined,
                    width: { ideal: 1280 },
                    height: { ideal: 720 },
                },
                audio: false,
            };

            // Проверяем поддержку getUserMedia
            if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
                toast.error("Ваш браузер не поддерживает getUserMedia");
                setLoadingLibraries(false);
                return;
            }

            console.log("Запрос на getUserMedia:", constraints);
            const stream = await navigator.mediaDevices.getUserMedia(constraints);
            console.log("Получен поток видео:", stream);

            if (videoRef.current) {
                videoRef.current.srcObject = stream;
                await videoRef.current.play();
            }
            // Создаём ImageCapture для "сырого" снимка
            const track = stream.getVideoTracks()[0];
            setImageCapture(new window.ImageCapture(track));

            setLoadingLibraries(false);
        } catch (error) {
            console.error("Ошибка доступа к камере:", error);
            toast.error("Не удалось получить доступ к камере. Проверьте разрешения.");
            setLoadingLibraries(false);
        }
    };

    /**
     * 6. Основной цикл распознавания
     */
    useEffect(() => {
        let isMounted = true;
        let timerId;

        const recognitionLoop = async () => {
            if (!isMounted) return;

            // Если видео не готово, ждём
            if (!videoRef.current || videoRef.current.readyState !== 4) {
                timerId = setTimeout(recognitionLoop, 500);
                return;
            }

            // Пробуем найти лицо
            let detection;
            try {
                detection = await faceapi.detectSingleFace(
                    videoRef.current,
                    new faceapi.TinyFaceDetectorOptions()
                );
            } catch (err) {
                console.error("Ошибка при детекции лица:", err);
                detection = null;
            }

            // Если лицо не найдено или нет ImageCapture — повторяем
            if (!detection || !imageCapture) {
                console.log("Лицо не обнаружено или ImageCapture не готов. Повтор через 500ms.");
                timerId = setTimeout(recognitionLoop, 500);
                return;
            }

            // Есть лицо — делаем снимок
            let blob;
            try {
                blob = await imageCapture.takePhoto();
                console.log("Снимок получен через ImageCapture:", blob);
            } catch (err) {
                console.error("Ошибка при получении снимка:", err);
                timerId = setTimeout(recognitionLoop, 2000);
                return;
            }

            // Собираем данные для запроса
            const formData = new FormData();
            formData.append("file", blob, "snapshot.jpg");
            formData.append("terminalId", terminalId);
            formData.append("direction", direction);

            try {
                toast.loading("Проверка доступа...");
                const response = await fetch(BASE_URL + "/api/internal/access", {
                    method: "POST",
                    body: formData,
                });
                toast.dismiss();

                // if (!response.ok) {
                //     console.error("Сервер вернул ошибку HTTP:", response.status);
                //     setError(`Ошибка сервера: ${response.status}`);
                //     showErrorOverlay();
                //     // Повторим через 1.5с
                //     timerId = setTimeout(recognitionLoop, 1500);
                //     return;
                // }

                const data = await response.json();
                console.log("Ответ сервера:", data);

                if (!data.success) {
                    // Сервер сказал, что доступ не разрешён
                    playSound("./error.mp3");
                    setError(data.message || "Нет доступа");
                    showErrorOverlay();

                    timerId = setTimeout(recognitionLoop, 1000);
                    return;
                }

                // Успех
                playSound("./allow.mp3");
                setLastUserName(data.data.name);
                setPhotoUrl(data.data.photoUrl);
                showSuccessOverlay();

                // Задержка 4с
                timerId = setTimeout(recognitionLoop, 4000);
            } catch (err) {
                console.error("Ошибка при отправке снимка на сервер:", err);
                toast.dismiss();
                // Повторим через 2с
                timerId = setTimeout(recognitionLoop, 2000);
            }
        };

        // Функции для показа оверлеев
        const showErrorOverlay = () => {
            if (errorOverlayRef.current) {
                errorOverlayRef.current.style.opacity = 1;
                setTimeout(() => {
                    if (errorOverlayRef.current) {
                        errorOverlayRef.current.style.opacity = 0;
                    }
                }, 1000);
            }
        };

        const showSuccessOverlay = () => {
            if (fullScreenRef.current) {
                fullScreenRef.current.style.opacity = 1;
                setTimeout(() => {
                    if (fullScreenRef.current) {
                        fullScreenRef.current.style.opacity = 0;
                    }
                }, 3000);
            }
        };

        // Запускаем цикл
        recognitionLoop();

        // Чистим при размонтировании
        return () => {
            isMounted = false;
            if (timerId) clearTimeout(timerId);
        };
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [imageCapture, terminalId, direction]);

    /**
     * Функция воспроизведения аудио
     */
    const playSound = (src) => {
        const audio = new Audio(src);
        audio.play().catch((err) => console.warn("Не удалось воспроизвести звук:", err));
    };

    return (
        <div className="face-recognition-container">
            {/* Камера на весь экран */}
            <video
                ref={videoRef}
                className="camera-feed"
                autoPlay
                muted
                style={{ transform: `rotate(${rotation}deg)` }}
            ></video>

            {/* Верхний оверлей (логотип + часы) */}
            <div className="overlay-top">
                <img
                    src="https://kit.edu.kz/_next/static/media/kit-icon.ccdf24de.svg"
                    alt="KIT logo"
                    className="logo"
                    onDoubleClick={() => setShowControls(!showControls)}
                />
                <div className="clock">
                    {new Date().toLocaleTimeString("ru-RU", {
                        hour: "2-digit",
                        minute: "2-digit",
                    })}
                </div>
            </div>

            {/* Панель управления (открывается по двойному клику на лого) */}
            {showControls && (
                <div className="controls-panel">
                    <div className="control-item">
                        <label>ID турникета:</label>
                        <input
                            value={terminalId}
                            onChange={(e) => {
                                setTerminalId(e.target.value);
                                localStorage.setItem("terminalId", e.target.value);
                            }}
                        />
                    </div>
                    <div className="control-item">
                        <label>Направление:</label>
                        <input
                            value={direction}
                            onChange={(e) => {
                                setDirection(e.target.value);
                                localStorage.setItem("direction", e.target.value);
                            }}
                        />
                    </div>
                    <div className="control-item">
                        <label>Выберите камеру:</label>
                        <select
                            onChange={(e) => setSelectedCamera(e.target.value)}
                            value={selectedCamera || ""}
                        >
                            {cameras.map((camera, index) => (
                                <option key={camera.deviceId} value={camera.deviceId}>
                                    {camera.label || `Камера ${index + 1}`}
                                </option>
                            ))}
                        </select>
                    </div>
                    <div className="control-item">
                        <label>Поворот камеры:</label>
                        <select
                            onChange={(e) => setRotation(e.target.value)}
                            value={rotation}
                        >
                            <option value="0">Нет</option>
                            <option value="90">90°</option>
                            <option value="-90">-90°</option>
                        </select>
                    </div>
                </div>
            )}

            {/* Оверлей "Добро пожаловать" */}
            <div className="fullscreen-success" ref={fullScreenRef}>
                <div className="fullscreen-success__content">
                    <img alt="user" src={`${BASE_URL}${photoUrl}`} className="userPhoto" />
                    <h1>Добро пожаловать, {lastUserName}!</h1>
                </div>
            </div>

            {/* Оверлей ошибки */}
            <div className="fullscreen-error" ref={errorOverlayRef}>
                <div className="fullscreen-error__content">
                    <h1>{error}</h1>
                </div>
            </div>

            <Toaster />
        </div>
    );
};

export default FaceRecognition;
